<template>
	<div class="cover">
		<div class="background-tile">
		</div>
		<div class="canvas">
		</div>
		<div class="cover-text">
			Rachel Frantsen
		</div>
		<div class="clickable-overlay" @click="navigateNext">
		</div>
	</div>
</template>

<script>
// Vision: 3d particlescape with lines drawn only between nodes within a vertical zone
// Z-faded for depth and x/y boundary, opacity based on proximity to visible vertical zone
export default {
	name: 'cover',
	methods: {
		navigateNext() {
			const screenName = 'intro';
			let vm = this;
			this.$scrollTo(`#screen-${screenName}`, {
				onDone() {
					vm.$emit('navigate', screenName);
				},
				duration: 300,
				easing: 'ease-out',
				cancelable: true,
			});
		},
	},
};
</script>

<style scoped>
.cover {
	height: 100vh;
	width: 100vw;
	display: flex;
	justify-content: center;
	align-items: center;
}

.background-tile {
	position: absolute;
	width: 65%;
	height: 65%;
	background-color: lightslategray;
}

.canvas, .clickable-overlay {
	position: absolute;
	height: 100%;
	width: 100%;
}

.clickable-overlay {
	cursor: crosshair;
}

.cover-text {
	position: absolute;
	width: 61%;
	color: #fff;
	text-align: center;
	font-size: 7vmin;
	font-weight: bold;
	letter-spacing: .5vw;
}
</style>



